<template>
 <div class="wrapper">
   <div class = "list" v-if="movieList">
    <div class="content-wrapper"  v-for="(item ,index) in movieList">
       <div class = "avatar"><img :src="item.imgUrl"></div>
       <div class="content">
         <div class="title">{{item.title}}</div>
         <div class="actor mt">{{item.message}}</div>
         <div class="loadstar">
            <star :size="24" :score="item.score" class="dblock"></star>
         </div>
         <div class="rat mt lh">
           <span>{{item.score}}</span>
           <span>{{item.rating}}</span>
         </div>
         <div class="article mt lh">{{item.article}}</div>
       </div>
       <div class="collection">
         <div class="ver" @click="collect(item)">
           <img :src="getcollectsrc " v-if="item.content.length == 0" alt="">
           <img :src="getcollectsrc1" v-if="item.content.length" alt="">
           <p>收藏</p>
         </div>
         <div class="ver " @click="showPage(item)">
           <img :src="getdetailsrc" alt="">
           <p>详情</p>
         </div>
       </div>
       <con v-show=" collectp == item.title" :img="item.imgUrl" :score="item.score" :title="item.title"
       @tochange="toback"></con>
       <transition name="fade">
         <log v-if="getcollectShow"></log>
       </transition>
      </div>
    </div>
 </div>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  &.fade-enter-active, &.fade-leave-active
    transition: all 0.2s linear
    transform translate3d(0, 0, 0)

  &.fade-enter, &.fade-leave-active
    opacity: 1
    transform translate3d(100%, 0, 0)

.wrapper
   width: 100%
   overflow: auto
   position: relative
   height: 100%
   padding: 0 auto
   background: #EEEEEE
   .content-wrapper
      display: inline-block
      background: white
      width: 46%
      margin: 1% 1.1%
      padding: 0.053333rem
      border: 0.013333rem solid #CCCCCC
      .avatar
        width: 100%
        img
           width: 100%
           height: 4.666667rem
      .content
        color: rgb(77,85,93)
        height: 2.333333rem
      .actor
        height: 0.64rem
        line-height: 1
        overflow: hidden
      .lh
        font-size: 0.32rem
        overflow: hidden
        height: 0.32rem
        text-align: center
      .title
        font-weight: bold
        font-size: 0.373333rem
        text-align: center
        line-height: 1.5
      .article
        line-height: 1.2
      .dblock
         display: inline-block
      .loadstar
         text-align: center
      .collection
        font-size: 0.16rem
        color: rgb(77,85,93)
        height: 1.333333rem
        display: flex
        .ver
           flex: 1
           text-align: center
           img
             width: 0.7rem
             height: 0.7rem
           p
             font-size: 0.32rem

</style>
<script type="text/ecmascript-6">
  import log from '../log/log'
  import star from '../star/star'
  import con from '../content/content'
  import {mapGetters} from 'vuex'
  import { Indicator } from 'mint-ui'
   export default {
        data(){
           return {
             movieList:[],
             list:[],
             toShow : false,
             collectpic: '',
             collectp: '',
             load: true,
             allLoaded: false,
             text:'加载更多'

           };
         },
         created () {
            Indicator.open('加载中...');
            setTimeout(() => {
               this.$http.get('restful/cheerio')
                  .then((response) => {
                     this.list = response.body;
                     this.movieList = this.list;
                },(err) => {
                     console.log(err);
                })
                  .then(() => {
                     Indicator.close();
                })
            },1200)
        },
        mounted () {
        },
        methods : {
           loadBottom: function() {
               if(this.movieList.length == 250) {
                   window.clearTimeout(set);
                   return false;
               } else {
                var set = setTimeout(() => {
                  let last = this.movieList.length-1;
                  for (let i = 1; i <= 10; i++) {
                    this.movieList.push(this.list[last+i]);
                  }
                  console.log(this.movieList.length);
                  this.$nextTick(() => {
                       this.allLoaded = true;
                  })
                }, 2500);
               }
           },
           collect :function(item){
             var that = this;
             this.$http.post('restful/collect',{
                user:sessionStorage.username,
                ref:item.ref,
                num:item.num,
                title:item.title,
                score:item.score,
                imgUrl:item.imgUrl
             }).then((response) => {
                   if(response.body.msg == "收藏成功"){
                        item.content.push(1);
                   }
             })
           },
           showPage : function (item){
             this.collectp = item.title;
             var data = {
                title : item.title
             }
             this.$store.dispatch('togethistoryratinfos',data)
           },
           toback (msg) {
              this.collectp = msg
           }
        },
        computed : {
          ...mapGetters([
             'getcollectsrc',
             'getdetailsrc',
             'getcollectShow',
             'getcollectsrc1',
             'getloading'
          ])
         },
        components : {
           star,
           con,
           log
        },
         beforeRouteLeave: (to, from, next) => {
            window.c = document.body.scrollTop
            next()
        },
        beforeRouteEnter: (to, from, next) => {
            next()
            setTimeout(() => {
                document.body.scrollTop = window.c
            }, 0)
        }
   }
</script>



