<template>
<transition name="fade">
   <div class="con">
      <div class="p1">
         <mt-header :title="title" class="myhead">
          <router-link to="#" slot="left">
           <mt-button icon="back" @click="goback">返回</mt-button>
          </router-link>
          <mt-button icon="more" slot="right"></mt-button>
         </mt-header>
      </div>
      <div class="pic" >
        <img :src="img" >
        <div class="score">
          <div class="sub">导演:</div>
          <div class="sub">主演:</div>
          <div class="sub">评分:{{score}}</div>
        </div>
      </div>
      <div class="bg" :style="'backgroundImage:url('+img+')'">
      </div>
      <div class="middle">
         <div class="title"  @click ="show1" ref="menu1" style="color:#E81161">详情</div>
         <div class="title"  @click ="show2" ref="menu2">评论</div>
      </div>
      <div class="content" v-if="getshowcontent">
         赵客缦胡缨，吴钩霜雪明。
         银鞍照白马，飒沓如流星。
         十步杀一人，千里不留行。
         事了拂衣去，深藏身与名。
         闲过信陵饮，脱剑膝前横。
         将炙啖朱亥，持觞劝侯嬴。
         三杯吐然诺，五岳倒为轻。
         眼花耳热后，意气素霓生。
         救赵挥金槌，邯郸先震惊。
      </div>
      <div class="rat" v-if="getshowrat">
        <div class="room">
         <div v-for="item in gethistoryratingfos">
           <span @click="addFriend(item)">{{item.user}}发表评论:</span>
           <span>{{item.message}}</span>
         </div>
         <div v-for="item in getratinfos">
           <span class="sq"><img :src="getusersrc" alt=""/></span>
           <span @click="addFriend(item)">{{item.user}}发表评论:</span>
           <span>{{item.message}}</span>
         </div>
        </div>
        <div class="input">
           <input type="textarea" v-model="ratmess">
           <button @click="submess">发送</button>
        </div>
      </div>
   </div>
</transition>
</template>
<style lang="stylus" rel="stylesheet/stylus" scoped>
&.fade-enter-active, &.fade-leave-active
    transition: all 0.2s linear
    transform translate3d(0, 0, 0)

  &.fade-enter, &.fade-leave-active
    opacity: 1
    transform translate3d(100%, 0, 0)
  .con
      position: fixed
      top: 0
      left: 0
      width: 100%
      height: 100%
      z-index: 99
      background-color: white
      text-align: center
      opacity: 1
      overflow: hidden
      .p1
         height: 1.066667rem
         .myhead
            height: 1.066667rem
      .pic
         width: 80%
         height: 6.666667rem
         margin: 0 auto
         text-align: left
         display: flex
         img
           width: 60%
           height: 80%
           flex: 0 0 60%
           margin-top: 5%
         .score
            color: rgb(215,215,215)
            margin-top: 5%
            flex: 1
            margin-left: 3%
            .sub
               margin-top: 0.133333rem
               height: 0.4rem
      .bg
         position: absolute
         top: 1.066667rem
         left: 0
         width: 100%
         height: 6.666667rem
         overflow: hidden
         filter: blur(0.133333rem)
         opacity: 0.8
         z-index: -1
         background-size: 100% 100%

      .middle
         display: flex
         height: 1.066667rem
         background: #26A2FF
        .title
            display: block
            height: 100%
            font-size: 0.426667rem
            margin-top: 0.32rem
            flex: 1
            color:white
      .content
         height: 8.986667rem
         font-size: 0.373333rem
         text-align: left
         text-indent: 0.746667rem
         background: white
      .rat
         height: 8.986667rem
         font-size: 0.373333rem
         text-align: left
         background: white
         .room
           height: 7.2rem
           font-size: 0.373333rem
           overflow: auto
           text-align: left
           .sq
              img
                 width: 0.373333rem
                 height: 0.373333rem
                 border-radius: 50%
         .input
             width: 100%
             height: 1.786667rem
             display: flex
             background: rgb(244,244,244)
             input
               height: 1.2rem
               flex: 0 0 70%
               margin-top: 0.293333rem
               outline: none
               border: 1px solid rgb(244,244,244)
               border-radius: 0.066667rem
             button
               height: 70%
               flex: 1
               font-size: 0.48rem
               border: none
               border-radius: 0.133333rem
               margin-top: 0.293333rem
               outline: none
               color: white
               background: #26A2FF
</style>
<script type="text/ecmascript-6">
import { MessageBox } from 'mint-ui'
  import io from 'socket.io-client'
  import {mapGetters} from 'vuex'
    export default {
       data () {
          return {
             ratmess:''
          }
       },
       created () {
       },
       props : ['img','score','title'],
       methods : {
            submess () {
              if(this.ratmess!=''){
                 var data = {
                 user: this.getusername,
                 message : this.ratmess,
                 title: this.title
               }
               this.$store.dispatch('torat',data);
               this.ratmess = "";
              } else {
                  MessageBox('警告', '请输入内容');
              }

            },
            addFriend () {
                var obj = {
                    name1: "zz",
                    name2: "pp"
                }
                if(obj.name1 != obj.name2) {
                    this.getsocket.emit('add',obj);
                }
            },
            show1 () {
               this.$store.commit('setshowcontent',true);
               this.$store.commit('setshowrat',false);
               this.$refs.menu1.style.color='#E81161'
               this.$refs.menu2.style.color='white'
            },
            show2() {
              this.$store.commit('setshowcontent',false);
              this.$store.commit('setshowrat',true);
              this.$refs.menu2.style.color='#E81161'
              this.$refs.menu1.style.color='white'
            },
            goback () {
               this.$emit('tochange','');
            }
       },
      computed : {
        ...mapGetters([
           'getusername',
           'getratinfos',
           'gethistoryratingfos',
           'getshowcontent',
           'getshowrat',
           'getusersrc'
        ])
      }
    }
</script>



